<template>
  <select 
    v-model="modelValue" 
    @change="changeOption"
  >
    <option disabled value="">Выберите из списка</option>
    <option 
      v-for="option in options" 
      key="option.value"
      :value="option.value" 
    >
      {{ option.name }}
    </option>
  </select>
</template>

<script>
export default {
  name: "my-select",
  props: {
    modelValue: {
      type: String
    },
    options: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    changeOption(event) {
      this.$emit('update:modelValue', event.target.value)
    }
  }
};
</script>

<style scoped>
select{
  color:blue;
  border:1px solid blue;
}

select:focus{
  outline:none;
  box-shadow: 0 0 10px rgba(0, 0, 255, 0.5)
}

select:hover{
  background-color:blue;
  color:white;
}
</style>
