<template>
  <div>
    <h1>Страница с постами</h1>
    <my-input
        v-focus
      v-model="searchQuery"
      placeholder="Поиск..."
    />
    <div class="app__buttons">
      <my-button
      @click="showDialog"
    >
      <strong >Cоздать пост </strong>
    </my-button>
    <my-select
      v-model="selectedSort"
      :options="sortOptions"
    />
    </div>
    <my-dialog v-model:show="dialogVisible">
      <post-form 
        @create="createPost" 
      />
    </my-dialog>
    <post-list 
      v-bind:posts="sortedAndSearchedPosts"
      @remove="removePost"
      v-if="!isPostLoading"
    />
    <div v-else>
      <my-loading/>
    </div>

    <div v-intersection="loadMorePosts"
         class="observer">
    </div>

    <!-- пагинация страниц -->
    <!-- <div class="page__wrapper">
      <div 
        v-for="pageNumber in totalPages" 
        :key="pageNumber"
        class="page"
        :class= "{
          'current-page': page === pageNumber
        }"
        @click="changePage(pageNumber)"
      >
        {{ pageNumber }}
      </div>
    </div> -->
  </div>
</template>

<script>
import PostForm from "@/components/PostForm";
import PostList from "@/components/PostList";
import axios from 'axios';
import MyLoading from "@/components/UI/MyLoading";

export default {
  components: {
    MyLoading,
    PostList,
    PostForm,
  },
  data() {
    return {
      posts: [],
      dialogVisible: false,
      isPostLoading: false,
      selectedSort: '',
      searchQuery:'',
      page: 1,
      limit: 10,
      totalPages: 0,
      sortOptions: [
        {value: 'title', name: 'По названию'},
        {value: 'body', name: 'По содержимому'},
      ],
    };
  },
  methods: {
    createPost(post) {
      this.posts.unshift(post);
      this.dialogVisible = false;
    },
    removePost(post) {
      this.posts = this.posts.filter(p => p.id !== post.id);
    },
    showDialog(){
      this.dialogVisible = true;
    },
    // функция пагинации
    // changePage(pageNumber) {
    //   this.page = pageNumber;
    // },
    async fetchPosts() {
      try {
      this.isPostLoading = true;
      const responce = await axios.get('https://jsonplaceholder.typicode.com/posts', {params: {
        _page:this.page,
        _limit:this.limit,
      }
      });
      this.totalPages = Math.ceil(responce.headers['x-total-count'] / this.limit);
      this.posts = responce.data;
      console.log(responce);
      this.posts = responce.data;
      } catch (e) {
        alert('Ошибка')
      } finally {
        this.isPostLoading= false;
      }
    },

    async loadMorePosts() {
      try {
      this.page += 1;
      const responce = await axios.get('https://jsonplaceholder.typicode.com/posts', {params: {
        _page:this.page,
        _limit:this.limit,
      }
      });
      this.totalPages = Math.ceil(responce.headers['x-total-count'] / this.limit);
      this.posts = [...this.posts, ...responce.data]
      } catch (e) {
        alert('Ошибка')
      }
    }
  },

  mounted(){
    this.fetchPosts();
    // const options = {
    // rootMargin: '0px',
    // threshold: 1.0
    // }
    // const callback = (entries, observer) => {
    //     /* Content excerpted, show below */
    //   if(entries[0].isIntersecting && this.page < this.totalPages) {
    //     this.loadMorePosts()
    //   }
    // };
    // const observer = new IntersectionObserver(callback, options)
    // observer.observe(this.$refs.observer);
  },

  computed: {
    sortedPosts() {
      return [...this.posts].sort((post1, post2) =>post1[this.selectedSort]?.localeCompare(post2[this.selectedSort]))
    },
    sortedAndSearchedPosts() {
      return this.sortedPosts.filter(post => post.title.toLowerCase().includes(this.searchQuery.toLowerCase()))
    }
  },
  watch: {
    // page() {
    //   this.fetchPosts();
    // }  
  }
};
</script>

<style scoped>

.app__buttons {
  display: flex;
  justify-content: space-between;
  margin: 15px 0px;
}

.page__wrapper {
  display: flex;
  margin-top: 15px;
}

.page {
  padding: 5px;
  margin-left: 5px;
  font-size: 20px;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.05);
  cursor: pointer;
}

.current-page {
  background: blue;
  color: white;
}

.observer {
  height: 30px;
  background:transparent;
}
</style>
