<template>
  <div>
<!--    Реализация модуля vuex пример-->
<!--    <h1>{{$store.state.isAuth ? "Пользователь авторизован" : "Авторизуйтесь, чтобы использовать"}}</h1>-->
<!--    <h1>{{$store.getters.doubleLikes}}</h1>-->
<!--    <div>-->
<!--      <my-button @click="$store.commit('incrementLikes')">Лайк-->
<!--      </my-button>-->
<!--      <my-button-->
<!--          @click="$store.commit('decrementLikes')">Дизлайк-->
<!--      </my-button>-->
<!--    </div>-->


    <h1>Страница с постами</h1>
    <my-input
        :model-value="selectedSort"
        @update:model-value="setSearchQuery"
      placeholder="Поиск..."
    />
    <div class="app__buttons">
      <my-button
      @click="showDialog"
    >
      <strong >Cоздать пост </strong>
    </my-button>
    <my-select
      :model-value="selectedSort"
      @update:model-value="setSelectedSort"
      :options="sortOptions"
    />
    </div>
    <my-dialog v-model:show="dialogVisible">
      <post-form
        @create="createPost"
      />
    </my-dialog>
    <post-list
      v-bind:posts="sortedAndSearchedPosts"
      @remove="removePost"
      v-if="!isPostLoading"
    />
    <div v-else>
      <my-loading/>
    </div>

    <div v-intersection="loadMorePosts"
         class="observer">
    </div>
  </div>
</template>

<script>
import PostForm from "@/components/PostForm";
import PostList from "@/components/PostList";
import MyButton from "@/components/UI/MyButton";
import MySelect from "@/components/UI/MySelect";
import MyInput from "@/components/UI/MyInput";
import {mapState, mapGetters, mapActions, mapMutations} from 'vuex'
export default {
  components: {
    MyInput,
    MySelect,
    MyButton,
    PostList, PostForm
  },
  data() {
    return {
      dialogVisible: false,
    }
  },
  methods: {
    ...mapMutations({
      setPage: 'post/setPage',
      setSearchQuery: 'post/setSearchQuery',
      setSelectedSort: 'post/setSelectedSort',
    }),
    ...mapActions({
      loadMorePosts: 'post/loadMorePosts',
      fetchPosts: 'post/fetchPosts'
    }),
    createPost(post) {
      this.posts.push(post);
      this.dialogVisible = false;
    },
    removePost(post) {
      this.posts = this.posts.filter(p => p.id !== post.id)
    },
    showDialog() {
      this.dialogVisible = true;
    },
  },
  mounted() {
    this.fetchPosts();
  },
  computed: {
    ...mapState({
      posts: state => state.post.posts,
      isPostsLoading: state => state.post.isPostsLoading,
      selectedSort: state => state.post.selectedSort,
      searchQuery: state => state.post.searchQuery,
      page: state => state.post.page,
      limit: state => state.post.limit,
      totalPages: state => state.post.totalPages,
      sortOptions: state => state.post.sortOptions
    }),
    ...mapGetters({
      sortedPosts: 'post/sortedPosts',
      sortedAndSearchedPosts: 'post/sortedAndSearchedPosts'
    })
  },
  watch: {
    // page() {
    //   this.fetchPosts()
    // }
  }
}
</script>


<style scoped>

.app__buttons {
  display: flex;
  justify-content: space-between;
  margin: 15px 0px;
}

.page__wrapper {
  display: flex;
  margin-top: 15px;
}

.page {
  padding: 5px;
  margin-left: 5px;
  font-size: 20px;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.05);
  cursor: pointer;
}

.current-page {
  background: blue;
  color: white;
}

.observer {
  height: 30px;
  background:transparent;
}
</style>
